@import "base/variables";
@import "base/mixins";
@import "base/typography";
@import "base/manifest";
@import "vendor/normalize";
@font-face {
  font-family: 'fontello';
  src: url('../fonts/fontello.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
body {
    font-family: $font-text, Arial, sans-serif;
    width: 100%;
    height: 100%;
    line-height: rem(22);
    font-size: rem(18);
    font-weight: 400;
    color: #474747;
}
header{
	width: 100%;
}
.flex-header{
	width: 100%;
}
fieldset {
	padding: 0;
}
.top-menu{
	width: 100%;
	min-height: 34px;
	background: #F7F7F7;
	.menuha{
		flex: none;
		ul{
			padding: 0;
			margin: 0;
			margin-right: 20px;
			li{
				display: inline-block;
				padding: 0 15px;
				position: relative;
				a{
					font-family: $ON;
					text-decoration: none;
					color: $red;
				}
				&:after{
					    content: ".";
					    position: relative;
					    font-size: 60px;
					    color: #FF8454;
					    right: -15px;
					    line-height: 0rem;
					    top: -1px;
				}
				&:first-child{
					padding-left: 0;
				}
				&:last-child{
					padding-right: 0;
					&:after{
						content: "";
					}
				}
				svg{
					width: 16px;
					height: 16px;
					fill: $green;
					display: none;
				}
			}
		}
	}
	.search{
		max-width: 490px;
		width: 100%;
		form{
			position: relative;
			input{
				margin-top: 4px;
				width: 100%;
				height: 26px;
				background: #FFFFFF;
				border-radius: 4px;
				border: none;
				padding: 0 35px 0 20px;
			}
			button{
				cursor: pointer;
				border: none;
				background: none;
				position: absolute;
				right: 8px;
				top: 7px;
				svg{
					width: 17px;
					height: 17px;
				}
			}
		}
	}
	.sing{
		flex: none;
		ul{
			margin: 0;
			padding: 0;
		}
		li{
			display: inline-block;
			margin-left: 16px;
			position: relative;
			top: 3px;
			a{
				color: $red;
				text-transform: uppercase;
				font-weight: 500;
				text-decoration: none;
				font-family: $ON;
			}
			&.singin{
				svg{
					width: 13px;
					height: 13px;
				}
			}
			&.singout{}
			&.singup{
				svg{
					width: 14px;
					height: 14px;
				}
			}
			svg{
				position: relative;
				left: -4px;
			}
		}
	}
}
svg.hidden{
	display: none;
}
.logo{
	.logotype{
		font-family: Oswald;
		font-style: normal;
		font-weight: 600;
		font-size: 44px;
		line-height: 45px;
		a{
			text-decoration: none;
		}
		span{
			font-weight: 500;
			font-size: 36px;
			margin-left: 18px;
		}
	}
	p{
		font-family: Roboto;
		font-style: normal;
		font-weight: normal;
		font-size: 12px;
		letter-spacing: 0.005em;
	}
}
.main-menu-block{
	margin-top: 20px;   
	.flex-main-menu-block{
		align-items: flex-start;
		width: 100%;
	}
	.address{
		padding: 0 15px;
		p{
			font-family: $ON;
			line-height: 24px;
			&:last-child{
				a{
					color: $red;
					text-decoration: underline;
				}
			}
		}
	}
	.phone{
		padding: 0 15px;
		.number{
			position: relative;
			a{
				font-family: $ON;
				color: $red;
				text-decoration: none;
				font-size: 16px;
				line-height: 24px

			}
			svg{
				width: 15px;
				height: 15px;
				position: absolute;
				top: 4px;
				left: -20px;
			}
		}
		.callback{
			margin-top: 4px;
			font-size: 13px;
			line-height: 15px
		}
	}
}

.bascket{
	height: 80px;
	width: 193px;
	border-radius: 4px;
	background-color: $blue;
	color: #fff;
	font-family: $ON;
	font-size: 14px;
	padding: 11px 15px;
	position: relative;
	.iconka{
		position: absolute;
		top: 17px;
		left: 15px;
		img{
			width: 38px;
			height: 38px;
		}
	}
	.flex-bascket{
		position: relative;
		left: 55px;
		.order{

		}
		.render-bascket{
			a{
				color: $red;
			}
		}
	}
}
.brands{
	margin-top: 20px;
	padding-bottom: 7px;
	.flex-brands{
		border-top: 1px solid #E8E8E8;
	    padding-top: 7px;
		width: 100%;
		justify-content: center;
	}
	.img{
		margin: 0 15px;
		img{
			display: none;
			max-width: 100%;
			width: 100%;
			height: auto;
		}
	}
}



/* Top panel */
.top_panel_wrap {
	position:relative;
}
.top_panel_fixed .top_panel_wrap {
	-webkit-box-shadow: -4px 3px 0px 0px rgba(0, 0, 0, 0.2);
	   -moz-box-shadow: -4px 3px 0px 0px rgba(0, 0, 0, 0.2);
			box-shadow: -4px 3px 0px 0px rgba(0, 0, 0, 0.2);	
}
.top_panel_over .top_panel_wrap {
	position:absolute;
	top:0;
	left:0;
	right:0;
	z-index:10000;
}
.top_panel_position_over.top_panel_wrap_inner,
.top_panel_position_over .top_panel_top,
.top_panel_position_over .top_panel_middle,
.top_panel_position_over .top_panel_bottom {
	background: none !important;
}
.admin-bar.top_panel_over .top_panel_wrap {
	top:32px;
}
.top_panel_wrap [class^="icon-"]::before, .top_panel_wrap [class*=" icon-"]::before {
	margin-right:0.5em;
}

/* Fixed panel */
.top_panel_fixed_wrap {
	display:none;
}
.top_panel_fixed:not(.top_panel_over) .top_panel_fixed_wrap {
	display:block;
}
.top_panel_fixed .top_panel_wrap {
	position:fixed;
	top:0;
	left:0;
	right:0;
	z-index:10000;
}
.sidebar_outer_left.top_panel_fixed .top_panel_wrap {
	left:290px;
}
.sidebar_outer_right.top_panel_fixed .top_panel_wrap {
	right:290px;
}
.top_panel_fixed .top_panel_top,
.top_panel_fixed .top_panel_style_1 .top_panel_middle,
.top_panel_fixed .top_panel_style_2 .top_panel_middle {
	display:none;
}
.top_panel_fixed .top_panel_style_5 .contact_logo {
	float: left;
}
.top_panel_fixed .top_panel_middle {
	min-height:0;
}
.top_panel_fixed .menu_main_wrap {
	margin-top:0;
}

.admin-bar.top_panel_fixed .top_panel_wrap {
	top:32px;
}


/* Top panel (top area) */
.top_panel_top {
	margin-top:0;
}
.top_panel_top_contact_area {
	float: left;
	margin-top:1em;
}

.top_panel_top_user_area {
	float:right;
}
.top_panel_top_socials {
	float:right;
}

.top_panel_top_open_hours,
.top_panel_top_search,
.menu_user_nav {
	float:left;
	margin-top:1em;
}
.menu_user_nav {
    font-size: 0.778em;
    text-transform: uppercase;
}
.top_panel_top_open_hours {
	margin-right:3em;
}
.top_panel_top_socials {
	margin-left:3em;
}
.top_panel_wrap .sc_socials a:before,
.top_panel_wrap .sc_socials [class*="icon-"]:before {
	margin-right:0;
}

.top_panel_wrap ul {
	margin:0;
	padding:0;
	list-style-type:none;
}
.top_panel_wrap li {
	margin-bottom: 0;
	position:relative;
}
.top_panel_wrap li li > a {
	position:relative;
	display:block;
	padding:0.5em 1.5em;
}
.menu_user_login:before, .menu_user_logout:before {
    content: "\e83c";
    font-family: Fontello;
    font-size: 1.6em;
    position: absolute;
    top: 0.35em;
    left: -0.8em;
}
.top_panel_wrap li.menu_user_login, .top_panel_wrap li.menu_user_logout {
    margin-left: 2.2em;
}
.menu_main_nav > li.menu-item-has-children > a:after {
	content: '\e828';
	font-family: 'fontello';
	position:absolute;
	right:10px;
	top:50%;
	-webkit-transform: translateY(-50%);
	   -moz-transform: translateY(-50%);
	        transform: translateY(-50%);
}
.top_panel_wrap li li.menu-item-has-children > a:after {
	content: '\e836';
	font-family: 'fontello';
	position:absolute;
	right:1em;
    font-size: 0.7em;
	top:50%;
	-webkit-transform: translateY(-50%);
	   -moz-transform: translateY(-50%);
	        transform: translateY(-50%);
    border-radius: 50%;
    width: 1.5em;
    height: 1.5em;
    line-height: 1.5em;
    text-align: center;
}
.responsive_menu .top_panel_wrap li li.menu-item-has-children > a::after {
	display:none;
}

/* User menu */
.menu_user_nav > li {
	margin-left:1.5em;
}
.menu_user_nav > li a {
	font-weight:400;
}
.menu_user_nav > li > a {
	display: block;
	padding: 0.9em 0 0;
    line-height: 1.3em;
}
.menu_user_nav > li li {
	line-height:1.5em;
}
.menu_user_nav > li ul:not(.cart_list) {
	width:12em;
	padding: 0;
	border:1px solid #ddd;
}
.menu_user_nav > li.menu_user_bookmarks ul {
	width:15em;
}
.menu_user_nav > li > ul {
	left:0;
	top:3.6em;
}
.menu_user_nav > li > ul ul {
	left:12em;
	margin-left:4px;
	top:-0.5em;
}
.menu_user_nav > li > ul ul.submenu_left {
	left:-12em;
	margin-left:-1px;
}

.menu_user_nav > li.menu_user_controls .user_avatar {
	display: inline-block;
	margin-right: 0.4em;
	overflow: hidden;
	vertical-align: middle;
	-webkit-border-radius: 50%;
	   -moz-border-radius: 50%;
			border-radius: 50%;
	width: 1em;
	height: 1em;
	line-height:1em;
}

/* Main and user menu */
.menu_main_nav_area {
	position:relative;
	display:none;
}
.menu_show .menu_main_nav_area {
	display:block;
}

.menu_user_nav > li:first-child {
	margin-left:0;
}

.menu_user_nav > li,
.menu_main_nav > li {
	position:relative;
	float:left;
	z-index:20;
}
.menu_main_nav > li:first-child > a {
    border-left: 1px solid;
}
.menu_main_nav > li > a {
    border-right: 1px solid;
    box-sizing: border-box;
    font-weight: 400;
}
.menu_main_nav > li {
    box-sizing: border-box;
}
.menu_main_nav > li::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 5px;
    z-index: 1;
    transition: height .2s;
    -o-transition: height .2s;
    -ms-transition: height .2s;
    -moz-transition: height .2s;
    -webkit-transition: height .2s;
}
.menu_main_nav > li.current-menu-ancestor::before,
.menu_main_nav > li.current-menu-item::before,
.menu_main_nav > li:hover::before,
.menu_main_nav > li.blob_over::before{
    height: 100%;
    z-index: -1;
}
.menu_main_nav > li+li { z-index:19; }
.menu_main_nav > li+li+li { z-index:18; }
.menu_main_nav > li+li+li+li { z-index:17; }
.menu_main_nav > li+li+li+li+li { z-index:16; }
.menu_main_nav > li+li+li+li+li+li { z-index:15; }
.menu_main_nav > li+li+li+li+li+li+li { z-index:14; }
.menu_main_nav > li+li+li+li+li+li+li+li { z-index:13; }
.menu_main_nav > li+li+li+li+li+li+li+li+li { z-index:12; }
.menu_main_nav > li+li+li+li+li+li+li+li+li+li { z-index:11; }
.menu_user_nav > li { z-index:30; }
.menu_user_nav > li+li { z-index:29; }
.menu_user_nav > li+li+li { z-index:28; }
.menu_user_nav > li+li+li+li { z-index:27; }
.menu_user_nav > li+li+li+li+li { z-index:26; }
.menu_user_nav > li+li+li+li+li+li { z-index:25; }
.menu_user_nav > li+li+li+li+li+li+li { z-index:24; }
.menu_user_nav > li+li+li+li+li+li+li+li { z-index:23; }
.menu_user_nav > li+li+li+li+li+li+li+li+li { z-index:22; }
.menu_user_nav > li+li+li+li+li+li+li+li+li+li { z-index:21; }
.menu_user_nav > li ul,
.menu_main_nav > li ul {
	position:absolute;
	display:none;
	z-index:10000;
}

.menu_main_nav > li > a {
	display: block;
	padding:1em 2.67em;
}

.menu_main_nav > li ul {
	width:350px;
	padding: 0;
    font-weight: 300;
}
.menu_main_nav > li > ul {
	left:0;
	top:40px;
}
.menu_main_nav > li > ul li {
    border-top: 1px solid;
}
.menu_main_nav > li > ul li:first-child {
    border: 0;
}
.menu_main_nav > li > ul ul {
	left:351px;
	top:0;
}
.menu_main_nav > li > ul ul.submenu_left {
	left:-351px;
}
.menu_main_nav li a:before {
	margin-right:0.5em;
}

/* Main menu slider */
.menu_main_nav > li#blob {
	position: absolute;
	top: 0;
	z-index: 1;
	opacity:0;
}
.menu_main_nav > li.blob_over:not(.current-menu-item):not(.current-menu-parent):not(.current-menu-ancestor):hover,
.menu_main_nav > li.blob_over:not(.current-menu-item):not(.current-menu-parent):not(.current-menu-ancestor).sfHover {
	background-color: transparent;
	-webkit-box-shadow: none;
	-moz-box-shadow:    none;
	box-shadow:         none;
}

/* Currency */
.menu_user_nav > li.menu_user_currency > a {
	padding-left:1.6em;
	padding-right:0.2em;
}

/* Bookmarks */
.menu_user_nav > li.menu_user_bookmarks li > a {
	position:relative;
}

.menu_user_nav > li.menu_user_bookmarks .bookmarks_add {
	border-bottom:1px solid rgba(0,0,0, 0.5);
}
.menu_user_nav > li.menu_user_bookmarks .bookmarks_delete {
	position:absolute;
	right:0;
	top:50%;
	margin-top:-0.5em;
	margin-right:0.5em;
	width:1em;
	height:1em;
	line-height:1em;
	font-size:1.25em;
}

/* Cart button */
.top_panel_style_3 .top_panel_cart_button,
.top_panel_style_4 .top_panel_cart_button {
	display:block;
	padding-left:1.5em !important;
	padding-right:1.5em !important;
}
.top_panel_middle .contact_cart {
	position:relative;
}
.top_panel_middle .sidebar_cart {
	position:absolute;
	top: 4.5em;
	left:0;
	z-index:1000;
	display:none;
	float:none;
	text-align:left;
}
.top_panel_icon {
	padding:0;
}
.top_panel_icon .sidebar_cart {
	top: 3.5em;
	left:-0.7em;
}
.top_panel_style_5 .top_panel_icon .sidebar_cart,
.top_panel_style_6 .top_panel_icon .sidebar_cart,
.top_panel_style_7 .top_panel_icon .sidebar_cart {
	left:auto;
	right: -1em;
}
.top_panel_icon.search_wrap,
.top_panel_inner_style_5 .menu_main_responsive_button,
.top_panel_inner_style_6 .menu_main_responsive_button,
.top_panel_inner_style_7 .menu_main_responsive_button {
	width: 2.2857em;
	height: 2.2857em;
	line-height: 2.2857em;
	-webkit-border-radius: 1.15em;
	   -moz-border-radius: 1.15em;
	        border-radius: 1.15em;
}
.top_panel_icon .contact_cart_label,
.top_panel_icon .contact_cart_totals {
	display:none;
}
.top_panel_icon .contact_icon {
	display:inline-block;
	text-align:center;
}
.top_panel_icon .contact_icon:before {
	margin-right:0;
}
.top_panel_icon.menu_main_cart {
	position:relative;
}
.top_panel_icon .contact_icon,
.top_panel_icon .search_submit {
	width: 2.2857em;
	height: 2.2857em;
	line-height: 2.2857em;
}
.top_panel_icon.menu_main_cart .contact_icon {
	-webkit-border-radius: 1.15em;
	   -moz-border-radius: 1.15em;
	        border-radius: 1.15em;
}


/* Logo in the header */

.responsive_menu .top_panel_middle .logo,
.menu_relayout .top_panel_middle .logo {
	margin-bottom:0 !important;
}
.top_panel_fixed .top_panel_wrap .logo_main {
	display: none;
}
.top_panel_wrap .logo_fixed {
	display:none;
	max-height:42px;
	width:auto;
}
.top_panel_fixed .top_panel_wrap .logo_fixed {
	display: block;
}
.top_panel_fixed .top_panel_middle .menu_main_wrap {
	margin-top:0;
}

.top_panel_style_5 .top_panel_wrap_inner {
	text-align:center;
}
.top_panel_style_5 .contact_logo {
	overflow:hidden;
}
.top_panel_style_5 .menu_main_wrap {
	display:inline-block;
	margin-top:0 !important;
}
.top_panel_style_5 .menu_main_nav_area,
.top_panel_style_5 .top_panel_icon {
	display:block !important;
	float: left;
}
.top_panel_style_5 .top_panel_top_user_area {
	text-align:left;
}

.top_panel_style_6 .menu_main_nav_area,
.top_panel_style_7 .menu_main_nav_area {
	float:none;
}
.top_panel_style_6 .top_panel_icon,
.top_panel_style_7 .top_panel_icon {
	float: right;
}

/* Featured image in the header 7 */
.top_panel_image {
	height:45em;
	overflow:hidden;
	position:relative;
	background-size:cover;
	background-position:center;
}
.top_panel_image_hover {
	position:absolute;
	top:0;
	left:0;
	right:0;
	bottom:0;
	background-color:rgba(2,75,94, 0.8);
}
.top_panel_image_header {
	position:absolute;
	left:50%;
	top:50%;
	display:block;
	max-width:50%;
	text-align:center;
	-webkit-transform:translateX(-50%) translateY(-50%);
	   -moz-transform:translateX(-50%) translateY(-50%);
	        transform:translateX(-50%) translateY(-50%);
}
.top_panel_image_icon {
	font-size: 4em;
	line-height:1em;
	margin-top:1em;
}

/* Relayout menu */
.menu_relayout .top_panel_style_1 .menu_main_nav_area:after,
.menu_relayout .top_panel_style_2 .menu_main_nav_area:after,
.menu_relayout .top_panel_style_5 .menu_main_nav_area:after,
.menu_relayout .top_panel_style_6 .menu_main_nav_area:after,
.menu_relayout .top_panel_style_7 .menu_main_nav_area:after {
	content:' ';
	clear: both;
	display:block;
	width:100%;
	height:0;
}
.menu_relayout .top_panel_style_3 .contact_logo,
.menu_relayout .top_panel_style_3 .menu_main_wrap,
.menu_relayout .top_panel_style_4 .contact_logo,
.menu_relayout .top_panel_style_4 .menu_main_wrap,
.menu_relayout .top_panel_style_6 .contact_logo,
.menu_relayout .top_panel_style_6 .menu_main_wrap,
.menu_relayout .top_panel_style_7 .contact_logo,
.menu_relayout .top_panel_style_7 .menu_main_wrap {
	width: 100%;
	text-align:center;
}
.menu_relayout .top_panel_style_3 .menu_main_wrap,
.menu_relayout .top_panel_style_4 .menu_main_wrap,
.menu_relayout .top_panel_style_6 .menu_main_wrap,
.menu_relayout .top_panel_style_7 .menu_main_wrap {
	margin-top: 0;
}
.menu_relayout .top_panel_style_5 .menu_main_nav_area,
.menu_relayout .top_panel_style_6 .menu_main_nav_area,
.menu_relayout .top_panel_style_7 .menu_main_nav_area {
	float: none !important;
	display:block !important;
	text-align:center;
}
.menu_relayout .top_panel_style_5 .menu_main_nav,
.menu_relayout .top_panel_style_6 .menu_main_nav,
.menu_relayout .top_panel_style_7 .menu_main_nav {
	display:inline-block;
	text-align:left;
}
.menu_relayout .top_panel_style_5 .menu_main_wrap,
.menu_relayout .top_panel_style_6 .menu_main_wrap,
.menu_relayout .top_panel_style_6 .content_wrap,
.menu_relayout .top_panel_style_7 .menu_main_wrap,
.menu_relayout .top_panel_style_7 .content_wrap {
	text-align:center;
}
.menu_relayout .top_panel_icon {
	float:none !important;
	margin-top: 0;
	margin-left: 1em;
	margin-right: 1em;
	display:inline-block !important;
	vertical-align:top;
}


/* Responsive menu */
.menu_main_responsive_button {
	display:none;
}
.menu_main_responsive_button:before {
	font-size:3em;
